import Chicken from "./Chicken";
import React, {useEffect, useState} from "react";
import PropTypes from 'prop-types'

import data from './Chickens.json'

function ChickenShelf() {
    const [chickens, setChickens] = useState()
    const [isLoaded, setLoaded] = useState(false)
    const [error, setError] = useState()

    useEffect(() => {
        //  to enable node backend uncomment this
        // fetch("http://localhost:8001/api/chickens")
        //     .then(res => res.json())
        //     .then(
        //         (result) => {
        //             setLoaded(true)
        //             setChickens(result)
        //         },
        //         (error) => {
        //             setLoaded(true)
        //             setError(error)
        //         }
        //     )
        setLoaded(true)
        setChickens(data)
    }, []);

    console.log(chickens)

    if (error) {
        return <div>Error: {error.message}</div>
    } else if (!isLoaded) {
        return <div>Loading...</div>
    } else {
        return (
            <div className="eggshelf">
                {chickens.map(chicken => (
                    <Chicken key={chicken.id} {...chicken} photo={require('./chickens/' + chicken.name + '.jpg')} />
                ))}
            </div>
        )
    }
}

ChickenShelf.propTypes = {
    isLoaded: PropTypes.bool,
    chickens: PropTypes.arrayOf(
        PropTypes.shape({
            id: PropTypes.number.isRequired,
            name: PropTypes.string.isRequired,
            // photo: PropTypes.func.isRequired,
            // description: PropTypes.string.isRequired,
            eggPrice: PropTypes.number.isRequired
        }).isRequired
    ).isRequired,
    error: PropTypes.object
}

ChickenShelf.defaultProps = {
    isLoaded: false,
    chickens: data,
    error: {}
}

export default ChickenShelf